// src/Loader.js
import React, { useEffect, useState } from 'react';
import { motion, useAnimation } from 'framer-motion';

const Loader = ({ onLoadingComplete }) => {
  const [progress, setProgress] = useState(0);
  const [isLoading, setIsLoading] = useState(true); // Track loading state
  const textControls = useAnimation(); // Controls for text color animation

  useEffect(() => {
    const interval = setInterval(() => {
      setProgress((prevProgress) => {
        if (prevProgress >= 100) {
          clearInterval(interval);
          setIsLoading(false); // Stop loading when progress reaches 100%
          setTimeout(onLoadingComplete, 0); // Notify parent that loading is complete after current render
          return 100;
        }
        return prevProgress + 1;
      });
    }, 30); // Adjust the interval to control the speed of the loader

    return () => clearInterval(interval);
  }, [onLoadingComplete]);

  // Update text color based on progress
  useEffect(() => {
    if (progress > 50) {
      textControls.start({ color: '#000000' }); // Change text color to black
    } else {
      textControls.start({ color: '#FFFFFF' }); // Change text color back to white
    }
  }, [progress, textControls]);

  // If loading is complete, return null to hide the loader
  if (!isLoading) {
    return null;
  }

  return (
    <div className="h-screen flex items-center justify-center bg-black relative overflow-hidden">
      {/* Moving Container */}
      <motion.div
        className="absolute top-0 left-0 h-full bg-white"
        style={{ width: `${progress}%` }}
        initial={{ width: '0%' }}
        animate={{ width: `${progress}%` }}
        transition={{ duration: 0.3 }}
      />

      {/* Centered Text */}
      <motion.div
        className="relative z-10 text-center"
        animate={textControls} // Apply color animation
      >
        <h1 className="text-4xl font-anton">Shayan K.</h1>
        <p className="text-[16px] mt-2 font-mono">{`<loading ${progress}%>`}</p>
      </motion.div>
    </div>
  );
};

export default Loader;