"use client"

import { useState, lazy, Suspense } from "react"
import { motion, AnimatePresence } from "framer-motion"
import "./App.css"
import Header from "./Components/Header"
import Loader from "./Components/Context/Loader"
import { useCursor } from "./Components/Context/CustomCursor"
import LenisScroller from "./hooks/LenisScroll"

// Lazy load components
const Hero = lazy(() => import("./Components/Hero"))
const About = lazy(() => import("./Components/About"))
const Projects = lazy(() => import("./Components/Projects"))
const Footer2 = lazy(() => import("./Components/Footer2"))

const FuzzyOverlay = () => {
  return (
    <motion.div
      initial={{ transform: "translateX(-10%) translateY(-10%)" }}
      animate={{
        transform: "translateX(10%) translateY(10%)",
      }}
      transition={{
        repeat: Number.POSITIVE_INFINITY,
        duration: 0.6,
        ease: "easeInOut",
        repeatType: "mirror",
      }}
      className="bg-black-noise pointer-events-none absolute -inset-[100%] opacity-[15%] z-10"
    />
  )
}

export default function App() {
  const { cursorVariant, variants } = useCursor()
  const [isLoading, setIsLoading] = useState(true)

  const handleLoadingComplete = () => {
    setIsLoading(false)
  }

  return (
    <LenisScroller>
      <AnimatePresence mode="wait">
        {isLoading ? (
          <Loader key="loader" onLoadingComplete={handleLoadingComplete} />
        ) : (
          <motion.div
            key="content"
            initial={{ y: "100vh" }}
            animate={{ y: 0 }}
            transition={{ duration: 0.8, ease: "easeInOut" }}
            className="relative overflow-hidden font-matter"
          >
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              transition={{ duration: 0.5, delay: 0.5 }}
              className="relative"
            >
              <Header />
              <Suspense fallback={<div>Loading...</div>}>
                <main>
                  <Hero />
                  <About />
                  <Projects />
                  <Footer2 />
                </main>
              </Suspense>
            </motion.div>

            {/* Custom cursor */}
            <motion.div
              className="cursor"
              variants={variants}
              animate={cursorVariant}
              transition={{ duration: 0.1,  ease: "easeIn"}}
            />

            {/* Fuzzy overlay on top of everything */}
            <FuzzyOverlay />
          </motion.div>
        )}
      </AnimatePresence>
    </LenisScroller>
  )
}

